<template>
  <div class="early-warning-router-container">
    <div class="main-card">
      <div class="main-header">
        <span class="main-title">数据分析</span>
        <div class="header-title">地表水断面分析</div>
        <div class="close-btn" @click="cancelBtn">×</div>
      </div>
      <div class="content1">
        <div class="buttomBox">
          <div class="contentBox">
            <div class="top_box">
              <div class="top_center">
                <button @click="detailClk('1','1,2')" :class="modelShow==1 ? 'active' : '' ">国、市考核断面</button>
                <button @click="detailClk('2','3')" :class="modelShow==2 ? 'active' : '' ">镇(街道）考核断面</button>
                <!-- <button @click="detailClk('3','4')" :class="modelShow==3 ? 'active' : '' ">区级评价村社区断面</button> -->
              </div>
            </div>
            <div class="content_div">
              <div class="table-box">
                <div class="my-table-header">
                  <span style="width: 200px">断面名称</span>
                  <span style="width: 200px">河流</span>
                  <span style="width: 200px">考核断面</span>
                  <span style="width: 200px">水质目标类别</span>
                  <span style="width: 200px">当月达标情况</span>
                  <span style="width: 200px">本年平均水质类别</span>
                  <span style="width: 200px">本年达标情况</span>
                </div>
                <div class="my-table-body">
                  <vue-seamless-scroll :data="tableData" class="seamless-warp" :class-option="classOption">
                    <a href="javascript:;" @click="goToDetails(item)" v-for="(item, index) in tableData" :key="index" class="my-table-content">
                      <div class="text-span">{{ item.name }}</div>
                      <div class="text-span">{{ item.river }}</div>
                      <div class="text-span">{{ item.type }}</div>
                      <div class="text-span">{{ item.wq_object }}</div>
                      <div class="text-span">{{ item.wq_reach }}</div>
                      <div class="text-span">{{ item.wq_year_level }}</div>
                      <div class="text-span">{{ item.wq_year_reach }}</div>
                    </a>
                  </vue-seamless-scroll>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { http } from "@/utils/request";
export default {
  components: {},
  data() {
    return {
      modelShow: "1",
      tabeName: "1,2",
      // 表格数据
      tableData: [],
      list: [{ name: "1,2" }, { name: "3" }, { name: "4" }],
    };
  },
  created() {
    this.getData();
    this.getAirsDataList();
  },
computed: {
    classOption() {
      return {
        step: this.modelShow == '1'? 0 : 0.5, // 数值越大速度滚动越快
        limitMoveNum: 10, // 开始无缝滚动的数据量 this.dataList.length
        hoverStop: true, // 是否开启鼠标悬停stop
        direction: 1, // 0向下 1向上 2向左 3向右
        // openWatch: true, // 开启数据实时监控刷新dom
        singleHeight: 0, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
        singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
        waitTime: 1000, // 单步运动停止的时间(默认值1000ms)
      };
    },
  },
  methods: {
    detailClk(val, e) {
      this.modelShow = val;
      this.tabeName = e;
      // console.log('66,val',val,e)
      this.getAirsDataList();
    },
    cancelBtn() {
      this.$router.push({ name: "Home" });
    },
    getData() {},
    getAirsDataList() {
      let json = {
        username: "api",
        password: "a123456b",
        section_type: this.tabeName,
      };
      http
        .get(
          "https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/water/section-data",
          {
            params: {
              json,
            },
          }
        )
        .then((res) => {
          let resData = res.data.result;
          if (res.data.code === 2000 && !!resData) {
            this.tableData = resData;
            //   this.tableData = res.map(item =>{

            //         return{
            //             name:item?.name ? item.name : '',
            //             name:item?.name ? item.name : '',
            //             name:item?.name ? item.name : '',
            //             name:item?.name ? item.name : '',
            //             name:item?.name ? item.name : '',
            //             name:item?.name ? item.name : '',
            //         }
            //     })
          }
        });
    },
  },
};
</script>

