  width: 2054px;
  height: 969px;
  box-sizing: border-box;
  .main-header {
    position: relative;
  }
  .header-title {
    font-size: 30px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    color: #eefaff;
    line-height: 42px;
    text-align: center;
    position: absolute;
    top: 80px;
    left: 52px;
    width: 326px;
    height: 50px;
    line-height: 50px;
    background: url("../../assets/zxgz/zxgz-title-bg.png") no-repeat;
    background-size: 100% 100%;
  }
  .content1 {
    width: 1919px;
    height: 730px;
    .buttomBox {
      margin-top: 125px;
      position: relative;
      //   margin-top: 20px;
      width: 1919px;
      height: 710px;
      background: url("../../assets/dbsdmfx-bg.png") 0 no-repeat;
      background-size: 100% 100%;
      .contentBox {
        width: 1919px;
        height: 720px;

        .top_box {
          width: 1919px;
          height: 120px;
          .top_center {
            // width: 1300px;
            // margin: 0 auto;
            display: flex;
            justify-content: center;
            button {
              margin-top: 40px;
              width: 240px;
              height: 50px;
              margin-right: 33px;
              font-size: 21px;
              background: url("../../assets/button-bg.png") 0 no-repeat;
              color: #fff;
              border: none;
              cursor: pointer;
              &.active {
                background: url("../../assets/button-active-bg.png") 0 no-repeat;
              }
            }
          }
        }
        .content_div {
          width: 1919px;
          height: 580px;
          margin: 0 auto;
          .table-box {
            .my-table-header {
              font-size: 24px;
              color: #e4f1ff;
              width: 1919px;
              height: 59px;
              line-height: 59px;
              background: linear-gradient(
                to right,
                rgba(74, 129, 169, 0) 0%,
                rgba(65, 151, 227, 0.75) 50%,
                rgba(74, 129, 169, 0) 100%
              );
              opacity: 0.7;
            }
            .my-table-body {
              max-height: 485px;
              overflow: hidden;
              font-size: 21px;
              .my-table-content {
                height: 59px;
                line-height: 59px;
                opacity: 1;
                .text-red,
                .text-green {
                  display: inline-block;
                  width: 120px;
                }
                .text-span {
                  width: 200px;
                  text-overflow: ellipsis;
                  white-space: nowrap;
                  overflow: hidden;
                }
                .text-span:hover {
                  // width: auto;
                  // text-align: center;
                  text-overflow: inherit;
                  overflow: visible;
                  // white-space: pre-line;
                  white-space: nowrap;
                }
              }
              .my-table-content:nth-child(2n + 1) {
                background: none;
              }
              .my-table-content:nth-child(2n) {
                background: linear-gradient(
                  to right,
                  rgba(74, 129, 169, 0) 0%,
                  #4187e3 50%,
                  rgba(74, 129, 169, 0) 100%
                );
              }
            }
          }
        }
      }
    }
  }
}
